.contextMenu__overlay {
  position: absolute;
  inset: 0;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}
.context__menu {
  position: absolute;
  right: 0;
  width: 50vw;
  background-image: url('https://educourseshub.com/wp-content/uploads/2024/02/Space-Stars-BG-4.png');
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  opacity: 0;
}
.context__menu_opened {
  opacity: 1;
  -webkit-transition: opacity 0.5s ease-in-out;
  -o-transition: opacity 0.5s ease-in-out;
  transition: opacity 0.5s ease-in-out;
}
.contextMenu__close {
  position: absolute;
  top: 50px;
  right: 50px;
  width: 32px;
  height: 32px;
  background-color: transparent;
  border: none;
  background-image: url('../../images/close.svg');
  background-repeat: no-repeat;
  background-position: center;
}
.contextMenu__nav {
  padding-top: 50%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  row-gap: 50px;
}

@media screen and (max-width: 767px) {
  .contextMenu__close {
    top: 20px;
    right: 20px;
  }
}
